import React, { useEffect, useState } from "react";
import { RootState } from "../../../Storage/Redux/store";
import { useSelector } from "react-redux";
import { apiResponse, cartItemModel } from "../../../Interfaces";
import { inputHelper } from "../../../Helper";
import { MiniLoader } from "../Common";
import { useInitiatePaymentMutation } from "../../../Apis/paymentApi";
import { useNavigate } from "react-router-dom";

export default function CartPickupDetails(){
    const[loading, setLoading] = useState(false); //    
    const shoppingCartFromStore: cartItemModel[] = useSelector(
        (state : RootState) => state.shoppingCartStore.cartItems ?? []
        //Store.ts type
    );
    //  (pickup Detail)   . 
    const userData = useSelector((state: RootState) => state.userAuthStore);

    let grandTotal = 0;
    let totalItems = 0;
    const initialUserData = { // =>   
        name: userData.fullName, //   
        email: userData.email,
        phoneNumber: "",
    };

    //    ,     
    shoppingCartFromStore?.map((cartItem: cartItemModel)=>{
        totalItems += cartItem.quantity?? 0;
        grandTotal += (cartItem.menuItem?.price?? 0) * (cartItem.quantity?? 0);
        return null;
    });
    
    const navigate = useNavigate();
    const [userInput, setUserInput] = useState(initialUserData);
    const [initatePayment] = useInitiatePaymentMutation(); // api  
    const handlerUserInput = (e: React.ChangeEvent<HTMLInputElement>)=>{
        const tempData = inputHelper(e, userInput);
        setUserInput(tempData);
    }

    useEffect(() => {
      setUserInput({
        name: userData.fullName,
        email: userData.email,
        phoneNumber: "",
      });
    }, [userData]);

    const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        setLoading(true);

        //  true     . 
        const {data} : apiResponse = await initatePayment(userData.id) //api    

        // const orderSummary = { grandTotal, totalItems };
        navigate("/payment",{
          state: { apiResult: data?.result, userInput },
        });
    };

    return(
        <div className="border pb-5 pt-3">
        <h2 className="text-center text-dark BookkMyungjo-Bd">
          
        </h2>
        <hr /> 
        {/*      */}
        <form onSubmit={handleSubmit} className="col-10 mx-auto">
          <div className="form-group mt-3">
             
            <input
              type="text"
              value={userInput.name}
              className="form-control"
              placeholder="name..."
              onChange={handlerUserInput}
              name = "name"
              required
            />
          </div>
          <div className="form-group mt-3">
            
            <input
              type="email"
              value={userInput.email}
              className="form-control"
              placeholder="email..."
              onChange={handlerUserInput}
              name = "email"
              required
            />
          </div>
  
          <div className="form-group mt-3">
            
            <input
              type="number"
              value={userInput.phoneNumber}
              className="form-control"
              placeholder="phone number..."
              onChange={handlerUserInput}
              name = "phoneNumber"
              required
            />
          </div>
          <div className="form-group mt-3">
            <div className="card p-3" style={{ background: "ghostwhite" }}>
              <h5>   : {grandTotal.toFixed(0)}</h5>
              <h5>   : {totalItems}</h5>
            </div>
          </div>
          <button
            type="submit"
            className="btn btn-lg btn-dark form-control mt-3"
            disabled={loading || shoppingCartFromStore.length == 0} //  . 
          >
            <div 
              className="HancomMalangMalang-Regular">
            {loading ? <MiniLoader /> : "" }
            </div>
          </button>
        </form>
      </div>
    )
}