import {useStripe, useElements, PaymentElement} from '@stripe/react-stripe-js';
import { useState } from 'react';
import { toastNotify } from '../../../Helper';
import { orderSummaryProps } from '../Order/orderSummaryProps';
import { apiResponse, cartItemModel, userModel } from '../../../Interfaces';
import { SD_Status } from '../../../Utility/SD';
import { useCreateOrderMutation } from '../../../Apis/orderApi';
import { useNavigate } from 'react-router-dom';
import { useUpdateShoppingCartMutation } from '../../../Apis/shoppingCartApi';
import { useDispatch, useSelector } from 'react-redux';
import { removeFromCart, updateQuantity } from '../../../Storage/Redux/shoppingCartSlice';
import { RootState } from "../../../Storage/Redux/store";

const PaymentForm = ({data, userInput} : orderSummaryProps) => {
  const navigate = useNavigate();
  const stripe = useStripe();
  const elements = useElements();
  const dispatch = useDispatch();
  const userData: userModel = useSelector(
    (state: RootState) => state.userAuthStore
  )
  const shoppingCartFromStore: cartItemModel[] = useSelector(
    (state : RootState) => state.shoppingCartStore.cartItems ?? []
    //Store.ts type
);
  const [updateShoppingCart] = useUpdateShoppingCartMutation();
  const [createOrder] = useCreateOrderMutation();
  const [isProcessing, setIsProcessing] = useState(false);
  // console.log("data");
  // console.log(data);

  //  
  const handleQuantity = (
    updateQuantityBy: number,
    cartItem: cartItemModel
  ) => {
    updateShoppingCart({
      menuItemId: cartItem.menuItem?.id,
      updateQuantityBy: 0,
      userId: userData.id, // id  
    });
    dispatch(removeFromCart({cartItem, quantity:0}));
  }

  const handleSubmit = async (e : React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (!stripe || !elements) {
      return;
    }
    
    setIsProcessing(true);
    const result = await stripe.confirmPayment({
      //`Elements`       
      elements,
      confirmParams: {
        return_url: "https://example.com/order/123/complete", //    url 
      },
      redirect: "if_required"
    });

    if (result.error) {
      // console.log(result.error.message);
      toastNotify("An unexpected error occured.", "error"); //      
      setIsProcessing(false);
    } else {
      console.log("result:",result);
      let grandTotal = 0;
      let totalItems = 0;
      const orderDetailsDTO: any = [];
      data.cartItems?.forEach((item:cartItemModel)=>{
        const tempOrderDetail: any = {};
        tempOrderDetail["menuItemId"] = item.menuItem?.id;
        tempOrderDetail["quantity"] = item.quantity;
        tempOrderDetail["itemName"] = item.menuItem?.name;
        tempOrderDetail["price"] = item.menuItem?.price;
        orderDetailsDTO.push(tempOrderDetail);
        grandTotal += item.quantity! * item.menuItem?.price!; //!   
        totalItems += item.quantity!;
      });

      const response: apiResponse = await createOrder({
        pickupName: userInput.name,
        pickupPhoneNumber: userInput.phoneNumber,
        pickupEmail: userInput.email,
        totalItems: totalItems,
        orderTotal: grandTotal,
        orderDetailsDTO: orderDetailsDTO,
        stripePaymentIntentID: data.stripePaymentIntentId,
        applicationUserId: data.userId,
        status:
          result.paymentIntent.status === "succeeded"
          ? SD_Status.CONFIRMED
          : SD_Status.PAYMENT,
      });

      if(response){
        if (response.data?.result.status === SD_Status.CONFIRMED) {
          navigate(
            `/order/orderConfirmed/${response.data.result.orderHeaderId}`
          );
        } else {
        navigate("/failed")
      }
    }
  }
    setIsProcessing(false);
  };
  //"status" : "string",
  return (
    <form onSubmit={handleSubmit}>
      <PaymentElement />
      <button
        disabled={!stripe || isProcessing}
        className='btn btn-dark mt-5 w-100'>
        <span id="button-text">
          {isProcessing ? "Processing ..." : ""}
        </span>
      </button>
    </form>
  );
};

export default PaymentForm;